import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Portfolio from '@/components/Portfolio'
import Contact from '@/components/Contact'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    }, {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio


    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    }

  ]

})



