<template>
<div id="About">
  <div class="wrapper">
    <div class="header"></div>
    <div class="footer"></div>
    <div class="col-right"></div>
      <div class="menu">
            <nav>
              <ul>
                <router-link
                    to="/"
                    tag="li"
                    v-bind:style="{cursor: 'pointer'}">
                    About
                </router-link>
                <router-link
                    to="/portfolio"
                    tag="li"
                    v-bind:style="{cursor: 'pointer'}">
                    Portfolio
                </router-link>
                <li><a href="mailto:madlangawapat22@gmail.com?Subject=Porfolio Patrick" target="_top">
                  Email
                  </a>
                </li>
              </ul>
            </nav>
      </div>

    <div class="col-middle-back" :style="{backgroundImage:'url('+ changeImage +')'}">

      <transition
          :name="imgAnimation"
          mode="out-in">
            <div class="col-middle"
              :key="changeImage"
              :style="{backgroundImage:'url('+ changeImage +')' }">

            </div>

      </transition>
    </div>

    <div class="col-left">
        <div class="content-about">
                <div class="title" >
                  <transition :name="contentAnimation" mode="out-in">
                    <h1 :key="title">{{title}}</h1>
                  </transition>
                </div>
                <div class="content">
                  <transition :name="contentAnimation" mode="out-in">
                    <p :key="description">{{description}}</p>
                    </transition>
                </div>
                <p></p>
          <div class="controls" style="display:inline-block; position: absolute;">
                  <button @click="contentAnimation='anim-titlel';
                          imgAnimation='anim-imagel'; currentIndex--;
                          disablePrevButton = true; disableNextButton = true;"
                          :disabled="disablePrevButton"
                          id="prev">
                          previous
                  </button>
                  <button @click="contentAnimation='anim-title';
                          imgAnimation='anim-image'; currentIndex++;
                          disableNextButton = true;
                          disablePrevButton = true;"
                          :disabled="disableNextButton"
                          id="next">
                          next
                  </button>
          </div>
      </div>
    </div>
  </div>
</div>

</template>

<script>

export default {
  name: 'About',
        data () {
          return {
            contents:[
              {
                "id": 1,
                "src": 'static/images/1.jpg',
                "title": "Developer",
                "body": "A four years experienced freelance software developer has given me enough knowledge to create a reliable system, a user friendly and secure application is my top priority and that might be the reason why I still survive working as a freelancer.",
                show : false
              },
              {
                "id": 2,
                "src": 'static/images/3.jpg',
                "title": "Designer",
                "body": "Along with my system development task, there will always be an extra job that I love to do, that is graphics design, and that might be the reason why most of my client considered me as a full-stack developer, but for me doing something like this is a passion.",
                show : false
              },
              {
                "id": 3,
                "src": 'static/images/4.jpg',
                "title": "Data Analyst",
                "body": "A proof that will show my excellence from my previous job, 3 years’ experience as a data analyst has given me enough knowledge but also give me determination to continue my career as a system developer.",
                show : false
              }
            ],
      contentAnimation: '',
      imgAnimation: '',
      currentIndex: 0,
      title:'',
      description:'',
      count: 0,
      disablePrevButton: true,
      disableNextButton: false
          }
        },
      created: function() {

          var doScroll = function (e) {
              e = window.event || e;
              var delta = Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)));
              this.direction = delta;
              if(this.direction == 1){
                  document.getElementById("prev").click();
                }
              else {
                  document.getElementById("next").click();
                }
              // console.log(delta);
              e.preventDefault();
          };

          if (window.addEventListener) {
              window.addEventListener("mousewheel", doScroll, false);
              window.addEventListener("DOMMouseScroll", doScroll, false);
          } else {
              window.attachEvent("onmousewheel", doScroll);
          }

      },
      computed: {
        changeImage () {

        setTimeout(() => {
              this.disableNextButton = false;
              this.disablePrevButton = false;
          if(this.currentIndex == 0){
              this.disablePrevButton = true;
            }
            else if(this.currentIndex == this.contents.length-1){
              this.disableNextButton = true;
            }
        }, 1000);
            this.title = this.contents[this.currentIndex].title;
            this.description = this.contents[this.currentIndex].body;
            this.image = this.contents[this.currentIndex].src;
            return this.contents[this.currentIndex].src;
        }
      },
      destroyed: function () {
          window.location.reload(true)
      }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url('../custom.css');
@import url('../custom-animation.css');


</style>




